.title {
  display: flex;
  justify-content: center;
  padding: 10px;
  font-weight: 700;
  font-size: 23px;
}
.daily-item {
  background-color: #f5f5f5;
  border-radius: 15px;
  margin: 10px;
  height: 40px;
  display: flex;
  align-items: center;
  cursor: pointer;
  padding: 5px 20px;
  font-size: 14px;
}
.icon-small {
  width: 40px;
}
.day {
  color: #242d49;
  flex: 1 1;
  font-weight: 600;
  margin-left: 15px;
}
.description {
  flex: 1 1;
  text-align: right;
  margin-right: 15px;
}
.min-max {
  color: #757575;
}
.daily-details-grid {
  display: grid;
  grid-row-gap: 0;
  grid-column-gap: 15px;
  column-gap: 35px;
  row-gap: 0;
  flex: 1 1;
  grid-template-columns: auto auto;
  padding: 5px 15px;
  background-color: #fad79d;
  border-radius: 15px;
  margin: 0 15px;
}
.daily-details-grid-items {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 30px;
}
.daily-details-grid-items label:first-child {
  color: #242d49;
}
.daily-details-grid-items label:last-child {
  color: #242d49;
  opacity: 0.8;
}
