.weather {
  width: 950;
  border-radius: 6px;
  box-shadow: 0px 19px 60px rgb(0 0 0/8%);
  color: #242d49;
  background-color: #fad79d;
  margin: 20px auto 0 auto;
  padding: 0 20px 20px 20px;
}
.top,
.bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.city {
  font-weight: 600;
  font-size: 18px;
  line-height: 1;
  letter-spacing: 1px;
  margin: 0;
}
.weather-description {
  font-weight: 400;
  font-size: 14px;
  line-height: 1;
  margin: 0;
}
.weather-icon {
  width: 100px;
}
.temperature {
  font-weight: 600;
  font-size: 70px;
  width: auto;
  letter-spacing: -5px;
  margin: 0;
}
.details {
  width: 20%;
  padding-left: 20px;
}
.parameter-row {
  display: flex;
  justify-content: space-between;
}
.parameter-label {
  text-align: left;
  font-weight: 400;
  font-size: 12px;
}
.parameter-value {
  text-align: right;
  font-weight: 600;
  font-size: 12px;
}
